rgin: 0;\n}\nfieldset {\r\n  border: 1px solid silver;\r\n  margin: 0 2px;\r\n  padding: .35em .625em .75em;\n}\nlegend {\r\n  border: 0;\r\n  padding: 0;\n}\ntable {\r\n  border-collapse: collapse;\r\n  border-spacing: 0;\r\n  overflow: hidden;\n}\na {\r\n  text-decoration: none;\n}\nblockquote {\r\n  border-left: 3px solid #D0E5F2;\r\n  font-style: normal;\r\n  display: block;\r\n  vertical-align: baseline;\r\n  font-size: 100%;\r\n  margin: .5em 0;\r\n  padding: 0 0 0 1em;\n}\nul,\r\nol {\r\n  padding-left: 20px;\n}\n.main-wrap {\r\n  max-width: 100%;\r\n  min-width: 300px;\r\n  max-width: 800px;\r\n  margin: 0 auto;\n}\n.content-wrap {\r\n  overflow: hidden;\r\n  background-color: #f9f9f9;\n}\n.content-wrap a {\r\n  word-break: break-all;\n}\n.headline {\r\n  border-bottom: 4px solid #f6f6f6;\r\n  color: #fff;\n}\n.headline-title.onlyheading {\r\n  margin: 20px 0;\n}\n.headline img {\r\n  width: 100%;\r\n  vertical-align: top;\n}\n.headline-background-link {\r\n  line-height: 2em;\r\n  position: relative;\r\n  display: block;\r\n  padding: 20px 45px 20px 20px !important;\n}\n.icon-arrow-right {\r\n  position: absolute;\r\n  top: 50%;\r\n  right: 20px;\r\n  background-image: url(http://static.daily.zhihu.com/img/share-icons.png);\r\n  background-repeat: no-repeat;\r\n  display: inline-block;\r\n  vertical-align: middle;\r\n  background-position: -70px -20px;\r\n  width: 10px;\r\n  height: 15px;\r\n  margin-top: -7.5px;\n}\n.headline-background .heading {\r\n  color: #999;\r\n  font-size: 15px!important;\r\n  margin-bottom: 8px;\r\n  line-height: 1em;\n}\n.headline-background .heading-content {\r\n  color: #444;\r\n  font-size: 17px!important;\r\n  line-height: 1.2em;\n}\n.headline-title {\r\n  line-height: 1.2em;\r\n  font-size: 22px;\r\n  margin: 20px 0 10px;\r\n  padding: 0 10px!important;\r\n  font-weight: bold;\r\n  position: absolute;\r\n  bottom: 10px;\r\n  font-size: 24px;\n}\n.headline .img-source {\r\n  position: absolute;\r\n  bottom: 2px;\r\n  right: 5